/**
**/
.c-button {
    display: inline-block;
    margin-bottom: 0;
    margin-right: 4px;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border-radius: 2px;
    white-space: nowrap;
    font-size: 16px;
    padding: 0 16px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
    background: getColor(grey-100);
    color: getColor(dark);
    transition: .3s ease-out;

    &:focus {
        text-decoration: none;
        color: getColor(dark);
    }

    &:hover {
        background: lighten(getColor(grey-100), 2%);
        color: getColor(dark);
    }

    &:last-child {
        margin-right: 0px;
    }
    
    span {
        display: inline-block;

        &:first-child {
            position: relative;
            z-index: 20;
        }
    }

    &__icon {
        width: 32px;
        height: 32px;
        vertical-align: text-top;
        display: inline-flex !important;
        position: relative;
        z-index: 10;

        svg {
            width: 7px;
            height: 10px;
            fill: #fff;
            margin: auto;
        }
    }

    &--bordered {
        background: transparent;
        color: #000;
        border: 1px solid #000;

        &:hover {
            color: #fff;
            background: #000;
        }
    }
}

button:disabled,
.button--disabled {
    opacity: 0.7;
    pointer-events: none;
    box-shadow: none;

    &:hover {
        color: inherit;
        background-color: inherit;
    }
}
