
/*
    <div class="content-block has-background">
        <div class="wrapper">
        </div>
    </div>
*/
.content-block {
    margin-top: $block_spacing;

    @include breakpoint(mobile) {
        margin-top: $block_spacing*2;
    }
    @include breakpoint(desktop) {
        margin-top: $block_spacing*3;
    }

    &:first-child {
        margin-top: 0;
    }

    &.has-background {
        padding: $block_spacing 0;

        @include breakpoint(mobile) {
            padding: $block_spacing*2 0;
        }

        @include breakpoint(desktop) {
            padding: $block_spacing*3 0;
        }

        + .has-background {
            margin-top: 0;
        }
    }
}