.menu {
	/* Default Menu CSS - if used without walker */
	list-style: none;
	padding: 0;

	/* Main Navigation */
	&--main-menu {
		display: none;

		@include breakpoint(tablet) {
      		display: block;
		}

		/* One Menu Item */
		.menu-item {
			position: relative;
			display: inline-block;

			/* Show Dropdown Menu Items on hover */
			&:hover {
				> .menu-item {
					&__dropdown {
						transform: translate(0);
						opacity: 1;
						visibility: visible;
					}
				}
			}

			&:hover {
				a {
					background: white;
					color: getColor(primary);
				}
			}

			/* Menu Item Link */
			a {
				display: block;
				padding: 15px 20px;
				font-weight: 400;
				color: getColor(dark);
			}

		}
	}

	// Footer Navigation
	&--footer-menu {
		margin-top: 15px;
		text-align: left;

		@include breakpoint(mobile) {
            		text-align: center;
		}

		.menu-item {
			margin-right: 10px;
			margin-bottom: 5px;

			@include breakpoint(mobile) {
				margin-right: 0;
			}

			a {
				color: getColor(dark);
				font-weight: 300;
			}
		}
	}
}
