body {
	font-size: 1.6rem;
	line-height: 1.5;
	font-family: $font_family;
}

a {
	color: $color_link;
	text-decoration: none;
	background-color: transparent;

	&:hover {
		color: $color_link_hover;
	}
}

p,
ul,
ol {
	margin: 0;
	padding: 0;
	margin-bottom: calc($component_margin/2);

	&:last-child {
		margin-bottom: 0;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 700;
	line-height: 1.2;
	margin: 0 0 $component_margin;

	&:last-child {
		margin-bottom: 0;
	}
}

@each $tag, $size in $mobile_headings {
	#{$tag},
	.#{$tag} {
		font-size: map-get($size, 'font-size');
		line-height: map-get($size, 'line-height');
	}
}

@each $tag, $size in $headings {
	@include breakpoint(mobile) {
		#{$tag},
		.#{$tag} {
			font-size: map-get($size, 'font-size');
			line-height: map-get($size, 'line-height');
		}
	}
}
