/**
 * Helpers
 */

.text {

    &-center {
        text-align: center;
    }

    &-left {
        text-align: left;
    }

    &-right {
        text-align: right;
    }

    &-justify {
        text-align: justify;
    }

    &-uppercase {
        text-transform: uppercase;
    }

    &-lowercase {
        text-transform: lowercase;
    }

    &-capitalize {
        text-transform: capitalize;
    }
}

.font {
    &-thin {
        font-weight: 100;
    }

    &-light {
        font-weight: 300;
    }

    &-regular {
        font-weight: 400;
    }

    &-medium {
        font-weight: 500;
    }

    &-semibold {
        font-weight: 600;
    }

    &-bold {
        font-weight: 700;
    }

    &-black {
        font-weight: 900;
    }
}

.mt {

    &-0 {
        margin-top: 0;
    }

    &-1 {
        margin-top: calc($component_margin/2);
    }

    &-2 {
        margin-top: $component_margin;
    }

    &-3 {
        margin-top: $component_margin*1.5;
    }

    &-4 {
        margin-top: $component_margin*2;
    }
}

.ml {

    &-0 {
        margin-left: 0;
    }

    &-1 {
        margin-left: calc($component_margin/2);
    }

    &-2 {
        margin-left: $component_margin;
    }

    &-3 {
        margin-left: $component_margin*1.5;
    }

    &-4 {
        margin-left: $component_margin*2;
    }
}

.mr {

    &-0 {
        margin-right: 0;
    }

    &-1 {
        margin-right: calc($component_margin/2);
    }

    &-2 {
        margin-right: $component_margin;
    }

    &-3 {
        margin-right: $component_margin*1.5;
    }

    &-4 {
        margin-right: $component_margin*2;
    }
}

.mb {

    &-0 {
        margin-bottom: 0;
    }

    &-1 {
        margin-bottom: calc($component_margin/2);
    }

    &-2 {
        margin-bottom: $component_margin;
    }

    &-3 {
        margin-bottom: $component_margin*1.5;
    }

    &-4 {
        margin-bottom: $component_margin*2;
    }
}

.pt {

    &-0 {
        padding-top: 0;
    }

    &-1 {
        padding-top: calc($component_margin/2);
    }

    &-2 {
        padding-top: $component_margin;
    }

    &-3 {
        padding-top: $component_margin*1.5;
    }

    &-4 {
        padding-top: $component_margin*2;
    }
}

// Creates
@each $tag,$color in $colors {
    .color-#{'' + $tag} {
        color: $color;
    }

    .background-#{'' + $tag} {
        background-color: $color;
    }
}